import * as React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Navbar from "./navbar"
import "./layout.scss"
import Footer from "./footer"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  React.useEffect(() => {
    window.addEventListener("scroll", function () {
      if (window.scrollY > (document.querySelector("section.hero").offsetHeight -80)) {
        document.getElementById("mainNav").classList.add("sticky-nav")
        // if (window.innerWidth >= 992) {
        //   document.body.style.paddingTop = '5em'
        // }
      } else {
        document.getElementById("mainNav").classList.remove("sticky-nav")
        // if (window.innerWidth >= 992) {
        //   document.body.style.paddingTop = 0
        // }
      }
    });
  }, []);

  return (
    <div className="container-fluid p-0">
      <Navbar siteTitle={data.site.siteMetadata?.title || `Title`} />
      <main>
        {children}
      </main>
      <Footer/>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
