const Navbar = ({ siteTitle }) => {
  return (
    <nav className="navbar fixed-top navbar-expand-lg" id="mainNav">
      <div className="container">
        <a href="/" className="navbar-brand">
          <i className="bi-hourglass-split"></i> Genoleum
        </a>

        {/* <a href="#" className="custom-btn btn btn-lg d-lg-none ms-auto me-4">
          Download
          <i className="bi-download ms-2"></i>
        </a> */}

        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav ms-lg-5 justify-content-end">
            <li className="nav-item">
              <a className="nav-link" href="#home">
                Home
              </a>
            </li>

            <li className="nav-item">
              <a className="nav-link" href="#about">
                About
              </a>
            </li>

            <li className="nav-item">
              <a className="nav-link" href="#process">
                Process
              </a>
            </li>

            {/* <li className="nav-item">
              <a className="nav-link" href="#section_4">
                Our Work
              </a>
            </li>

            <li className="nav-item">
              <a className="nav-link" href="#section_5">
                Pricing
              </a>
            </li> */}

            <li className="nav-item">
              <a className="nav-link" href="#contact">
                Contact
              </a>
            </li>
          </ul>

          {/* <div className="ms-auto d-none d-lg-block">
            <a href="#" className="custom-btn btn btn-lg">
              Download
              <i className="bi-download ms-2"></i>
            </a>
          </div> */}
        </div>
      </div>
    </nav>
  )
}

Navbar.propTypes = {
  siteTitle: PropTypes.string,
}

Navbar.defaultProps = {
  siteTitle: ``,
}

export default Navbar
